@import "../abstracts/mixins";
header {
    padding: 0;
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: $marginBottom-section;
    height: $navbar_height;
    width: 100%;
    label {
        font-size: 2.5em;
        z-index: 9999;
    }
    .logo {
        height: 35px;
        margin-left: 20px;
        img {
            margin-left: 0;
        }
    }
    label {
        margin-right: 20px;
    }

    button:active:focus {
        outline: none;
    }
    .sr-only {
        position: absolute;
        width: 1px;
        height: 1px;
        padding: 0;
        margin: -1px;
        overflow: hidden;
        clip: rect(0, 0, 0, 0);
        border: 0;
    }
    input {
        display: none;
    }
    input[type="checkbox"]:checked ~ nav {
        display: flex;
        justify-content: flex-start;
        padding-top: 45px;
    }
    input[type="checkbox"]:checked ~ label {
        color: white;
    }
}
/* Menu navigation */
nav {
    position: absolute;
    //Comportement
    display: none;
    flex-direction: column;
    justify-content: center;
    z-index: 900;
    transition: transform 0.3s ease;
    //Position
    top: 0;
    right: 0;
    //Dimension
    height: 100vh;
    min-height: 100vh;
    width: 320px;
    //Apparence
    background-color: $CG_Grey1;

    .menu_navigation {
        padding-inline-start: 20px;
        //Comportement
        display: flex;
        flex-direction: column;
        //Apparence
        list-style: none;
        li {
            //Comportement
            flex: 1;
            //Dimension
            height: 80px;
            span {
                margin-right: 10px;
            }
        }
        a {
            border-bottom: 1px solid $CG_Grey1;
            text-decoration: none;
            display: block;
            width: 65%;
            font-size: $sizeLienMenu--mobile;
            @include flex-row-verticalCenter;
            color: white;
            text-align: left;
        }
        a:hover,
        a:focus,
        a:active {
            color: $CG_RougePrincipal;
        }
    }
}
@include respond-to("M") {
    header {
        margin-bottom: 40px;
        nav {
            margin-right: 20px;
            position: relative;
            flex-direction: row;
            display: block;
            width: auto;
            height: 90px;
            background-color: transparent;
            height: $navbar_height;
            min-height: $navbar_height;
            .menu_navigation {
                flex-direction: row;
                justify-content: flex-end;
                width: auto;
                .miseEnAvant {
                    background-color: $CG_RougePrincipal;
                }
                li {
                    width: auto;
                    height: $navbar_height;
                    flex: auto;
                    align-items: center;
                    margin: 0px 20px;
                    a {
                        display: inline-block;
                        font-size: $sizeTextCTA;
                        text-align: center;
                        width: auto;
                        border-bottom: none;
                        color: $CG_Grey1;
                        font-weight: $medium;
                        span {
                            display: none;
                        }
                    }
                }
                .li-dropdown {
                    display: flex;
                    transition-duration: 0.5s;
                    font-size: $sizeTextCTA;
                    font-weight: $medium;
                    position: relative;
                    width: auto;
                }
                .li-dropdown:hover {
                    color: $CG_RougePrincipal;
                }
                ul {
                    visibility: hidden;
                    opacity: 0;
                    position: absolute;
                    transition: all 0.5s ease;
                    color: white;
                    top: $navbar_height;

                    li {
                        background-color: $CG_RougePrincipal;
                        clear: both;
                        font-size: $sizeTextParagraph;
                        align-items: flex-start;
                        margin: 0px;
                        white-space: nowrap;
                        justify-content: flex-start;
                        align-items: center;
                        height: 40px;
                        a {
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            color: white;
                            font-size: $sizeTextParagraph;
                            height: 100%;
                            width: 100%;
                            padding: 10px;
                        }
                        a:hover {
                            color: white;
                        }
                    }
                    li:hover {
                        background-color: $CG_RougePrincipalLight;
                    }
                    li:last-child {
                        a {
                            color: white;
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            font-weight: 500;
                            font-size: $sizeTextParagraph;
                            padding: 10px 20px;
                        }
                        a:hover{
                            border-radius: 0px;
                            background-color: $CG_RougePrincipalLight;
                        }
                    }
                }
                .li-dropdown:hover > ul,
                .li-dropdown ul:hover {
                    visibility: visible;
                    opacity: 1;
                    display: block;
                }
                li:last-child {
                    a {
                        @include a-button;
                    }
                }
            }
        }
    }
}
